@use "../../variables.modules" as variables;

.pink {
  color: variables.$pink;
}

.green {
  color: variables.$green;
}

.skills {
  columns: 1;

  @media only screen and (min-width: 940px) {
    columns: 2;
  }

  li {
    line-height: 1.75;

    @media only screen and (max-width: 768px) {
      line-height: 1.4;
    }
  }
}